import { Link } from "react-router-dom";
import React from "react";
import HOC from "./HOC";

export default function Home() {
  if (new Date().getFullYear() === 2023) throw new Error("Error roi!");
  return (
    <div>
      <p>Vui nha</p>
      <HOC>
        {[1].map((i) => (
          <div
            key={i}
            style={{
              width: 30,
              height: 30,
              backgroundColor: "blue",
              color: "red",
              position: "absolute",
              bottom: 10,
              right: 10,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: 50
            }}
          >
            {i}
          </div>
        ))}
      </HOC>
    </div>
  );
}
